import React, { useRef } from 'react';
import { auth } from '../firebase'                                 // this import is from the local firebase file
import './SignUpScreen.css'

function SignUpScreen() {

    const emailRef = useRef(null);
    const passwordRef = useRef(null);

     const register = (e) => {
         e.preventDefault();

         auth.createUserWithEmailAndPassword(           //take the current username and password, then authenticate the user. if error print the message using alert box
             emailRef.current.value,
             passwordRef.current.value
         ).then((authUser) => {
            console.log(authUser);
         }).catch(error => {
             alert(error.message);
         }) 
     }

     const signIn = (e) => {                        // it wont refresh the entire page when line 33 gets triggered
         e.preventDefault(); 

         auth.signInWithEmailAndPassword(
             emailRef.current.value,
             passwordRef.current.value
         ).then((authUser) => {
            console.log(authUser);
         }).catch(error => {
             alert(error.message);
         }) 
     }

    return (
        <div className="signUpScreen">
            <form>
                <h1> Sign In</h1>
                <input ref={emailRef} placeholder="Email" type="email" />
                <input ref={passwordRef} placeholder="Password" type="password" />
                <button type='submit' onClick={signIn}>Sign In</button>

                <h4>
                    <span className='signUpScreen_gray'>New to Netflix? </span> 
                    <span className='signUpScreen_link' onClick={register}>Sign up now.</span>
                </h4>
            </form>
        </div>
    )
}

export default SignUpScreen
